import * as React from 'react';
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import { School } from '@mui/icons-material';
import { Box, TextField , Typography } from '@mui/material';
import { useNavigate } from "react-router-dom";
import NavbarPage from '../Navbar/NavbarPage';

import ReactPlayer from 'react-player'



const MainPage = (props) => {
    const defaultValues = {
        ipaddress: "",
        email:""
      };
  const navigate = useNavigate();
  const [formValues, setFormValues] = React.useState(defaultValues);
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormValues({
      ...formValues,
      [name]: value,
      email:props.email
    });
  };
  const handleSubmit = async(event) => {
    event.preventDefault();
    console.log(defaultValues);
    
    window.location.replace(`http://${formValues.ipaddress}`);
    const {ipaddress,email}=formValues;
    const res = await fetch("https://hackathon-651c0-default-rtdb.firebaseio.com/ipaddres.json",{
        method:'POST',
        headers:{
            'Content-Type':'application/json'
        },
        body:JSON.stringify({
            email,
            ipaddress
        })
    });
  };
  return (
    
      <div>
        <NavbarPage email={props.email}/>
        <form onSubmit={handleSubmit}>
      <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      height="100vh"
      bgcolor="black"
      color="white"
      flexDirection={"column"}
      style={{ background: 'linear-gradient(135.8deg, rgb(26, 26, 29) 27.1%, rgb(26, 26,35) 77.5%)' }}
    >
      <Typography  variant="h2" component="h2" color="white" sx={{ m: 3 }}>
Register Your IP Adress     
 </Typography>
          <Typography  variant="h6" component="h2" color="white" sx={{ mx: 20 }} align="center">
          As you register your IP address you'll be redirected to page where you'll get your live feed and incoming threats</Typography>
          <TextField
          required
            style={{ width: '500px' , backgroundColor: 'white' }}
            id="margin-dense" 
            margin="normal"
            name="ipaddress"
            label="IP Adress"
            type="text"
            value={formValues.ipaddress}
            onChange={handleInputChange}
          />      
        <Button variant="contained" type="submit" id="margin-dense" margin="normal" style={{ backgroundColor: 'black', color: 'white' }}>
          Submit
        </Button>
    </Box>
    </form>
    {/* <ReactPlayer url={`http://${formValues.ipaddress}`} playing={true} controls={true} width="100%" height="auto"/> */}

      </div>
  );
};
export default MainPage;